.contenedor {
  max-width: 144rem;
  width: 95%;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

h1 {
  font-size: 5.3rem;
  color: #ffffff;
}

h2 {
  font-size: 5rem;
}

p {
  font-size: 1.8rem;
  color: #ffffff;
}

a {
  text-decoration: none;
  color: #ffffff;
  font-size: 1.6rem;
}
a img {
  margin-left: 3.5rem;
}

.header {
  background-image: url("/build/img/group.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  display: flex;
  height: 78.5rem;
}
.header .header__left {
  display: flex;
  justify-content: center;
  width: 7.3%;
}
.header .header__left .fill {
  width: 3.7rem;
  height: 3.7rem;
  margin-top: 0.9rem;
}
.header .header__line {
  height: 81.1rem;
  width: 0.1rem;
  border: solid 1px #ffffff;
}

.contenedor {
  margin-left: 2.5rem;
}

.header__right {
  display: flex;
  flex-direction: column;
  width: 92.6%;
}
.header__right .barra {
  display: flex;
  justify-content: space-between;
  margin-top: 4.5rem;
  letter-spacing: 0.15rem;
}
.header__right .barra.fijo {
  z-index: 10;
  position: fixed;
  padding: 1.5rem;
  width: 100%;
  background-color: rgba(255, 0, 0, 0.336);
  opacity: 0.92;
  margin: 0;
}
.header__right .barra .navegacion {
  width: 57.9rem;
}
.header__right .barra .navegacion a {
  margin-right: 2.5rem;
  transition: all 0.5s;
}
.header__right .barra .navegacion a:hover {
  color: #cbe346;
}
.header__right .barra .idioma {
  margin-right: 3.7rem;
}
.header__right .barra .idioma .español {
  font-weight: 700;
}
.header__right .barra .menu {
  display: none;
  filter: invert(100%);
}
.header__right .barra .fill {
  display: none;
}
.header__right .title {
  margin-top: 22.5rem;
  line-height: 1.28;
  letter-spacing: -0.05px;
}
.header__right .title .titulo span {
  color: #cbe346;
}
.header__right .btn {
  display: inline-block;
  background-color: none;
  border: 1px solid #ffffff;
  border-radius: 2.65rem;
  padding: 1.5rem 3.9rem 1.5rem 3rem;
  height: 5.3rem;
  text-transform: uppercase;
  transition: all 0.6s;
  width: 35rem;
}
.header__right .btn:hover {
  background-color: #ffffff;
  color: #cbe346;
}
.header__right .btn img {
  filter: invert(100%);
  margin: 0;
  width: 1.6rem;
  height: 0.6rem;
}

@media (max-width: 480px) {
  .header .header__left {
    display: none;
  }
  .header .header__line {
    display: none;
  }
  .header .header__right .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header .header__right .barra .navegacion {
    display: none;
  }
  .header .header__right .barra .menu {
    display: block;
    filter: invert(100%);
  }
  .header .header__right .barra .fill {
    display: block;
  }

  .title {
    display: flex;
    justify-content: center;
  }
  .title .titulo {
    width: 30rem;
    font-size: 3rem;
    text-align: center;
  }
  .title .btn {
    display: block;
    background-color: none;
    border: 1px solid #ffffff;
    border-radius: 2.65rem;
    padding: 1.5rem 3.9rem 1.5rem 3rem;
    height: 5.3rem;
    text-transform: uppercase;
    transition: all 0.6s;
  }
  .title .btn:hover {
    background-color: #ffffff;
    color: #cbe346;
  }
  .title .btn img {
    filter: invert(100%);
    margin: 0;
    width: 1.6rem;
    height: 0.6rem;
  }
}
.accesorio {
  height: 39.2rem;
  display: flex;
  background-color: #f3f4f8;
  padding: 0;
}
.accesorio .accesorio__left {
  width: 33.4%;
}
.accesorio .accesorio__left .accesorio__left__tennis {
  margin-left: 17.7%;
  text-align: end;
  color: #cbe346;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 60%;
}
.accesorio .accesorio__right {
  width: 38.6%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.accesorio .accesorio__right h2 {
  align-self: flex-start;
  color: #1e2335;
  font-size: 3rem;
  border-bottom: 16.9rem;
  text-align: center;
}
.accesorio .accesorio__right p {
  color: #787878;
}
.accesorio .accesorio__btn {
  width: 28%;
}
.accesorio .accesorio__btn .btn {
  display: inline-block;
  background-color: none;
  border: 1px solid #282e47;
  border-radius: 2.65rem;
  padding: 1.5rem 3.9rem 1.5rem 3rem;
  height: 5.3rem;
  text-transform: uppercase;
  transition: all 0.6s;
  color: #273048;
  margin-top: 69%;
}
.accesorio .accesorio__btn .btn:hover {
  background-color: #282e47;
  color: #cbe346;
}

@media (max-width: 480px) {
  .accesorio {
    display: block;
  }
  .accesorio .accesorio__left {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .accesorio .accesorio__left .accesorio__left__tennis {
    font-size: 2.5rem;
    margin: 2rem 0 0 0;
  }
  .accesorio .accesorio__right {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .accesorio .accesorio__right h2 {
    align-self: center;
    font-size: 2.5rem;
    border-bottom: 16.9rem;
  }
  .accesorio .accesorio__right p {
    margin: 2rem;
    color: #787878;
    text-align: center;
  }
  .accesorio .accesorio__btn {
    display: flex;
    justify-content: center;
    width: 100%;
  }
  .accesorio .accesorio__btn .btn {
    display: inline-block;
    background-color: none;
    border: 1px solid #282e47;
    border-radius: 2.65rem;
    padding: 1.5rem 3.9rem 1.5rem 3rem;
    height: 5.3rem;
    text-transform: uppercase;
    transition: all 0.6s;
    color: #273048;
    margin: 0;
    width: 95%;
  }
  .accesorio .accesorio__btn .btn:hover {
    background-color: #282e47;
    color: #cbe346;
  }
}
.racket {
  display: flex;
  height: 46.8rem;
}
.racket .racket__left {
  width: 36.8%;
  background-image: url("/build/img/ball-color-court-1405355.jpg");
  background-position: center center;
  background-size: cover;
}
.racket .racket__left .puntos {
  display: flex;
}
.racket .racket__left .puntos img {
  margin-right: 2.4rem;
}
.racket .racket__left .atras__adelante {
  display: flex;
  justify-content: flex-start;
  margin-top: 1rem;
}
.racket .racket__left .atras__adelante .oval__atras {
  border-radius: 50%;
  background-color: none;
  border: 1px solid #ffffff;
  width: 5.4rem;
  height: 5.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.racket .racket__left .atras__adelante .oval__adelante {
  margin-left: 1rem;
  border-radius: 50%;
  background-color: #cbe346;
  width: 5.4rem;
  height: 5.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.racket .racket__right {
  width: 63.2%;
}
.racket .racket__right .racket__right__imagenes {
  margin-top: 5.1rem;
  margin-left: 5.2rem;
  display: flex;
}
.racket .racket__right .racket__right__imagenes .contenedor__imagen {
  position: relative;
}
.racket .racket__right .racket__right__imagenes .contenedor__imagen .oval__numero {
  background-color: #cbe346;
  border-radius: 50%;
  width: 3.9rem;
  height: 3.9rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 1.1rem;
  left: 1.1rem;
}
.racket .racket__right .racket__right__imagenes .contenedor__imagen .imagen {
  width: 95%;
  margin-right: 3.6rem;
}
.racket .racket__right .racket__right__imagenes .contenedor__imagen .imagen2 {
  width: 95%;
  margin-right: 2.5rem;
}
.racket .racket__right .racket__right__imagenes .contenedor__imagen .imagen__media {
  width: 90%;
}

.racket__contenedor {
  margin-left: 37.7%;
}
.racket__contenedor h2 {
  width: 48.9%;
  color: #ffffff;
  font-weight: 300;
}
.racket__contenedor h2 span {
  font-weight: 700;
}
.racket__contenedor p {
  width: 57.2%;
  font-size: 1.8rem;
  line-height: 1.67;
  letter-spacing: 0.51px;
}

@media (max-width: 480px) {
  .racket {
    height: auto;
    display: block;
  }
  .racket .racket__left {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .racket .racket__left .racket__contenedor {
    width: 90%;
    margin: 0;
  }
  .racket .racket__left .racket__contenedor h2,
.racket .racket__left .racket__contenedor p {
    text-align: center;
    margin: 0 auto;
    width: auto;
  }
  .racket .racket__left .racket__contenedor .puntos {
    justify-content: center;
  }
  .racket .racket__left .racket__contenedor .atras__adelante {
    justify-content: center;
  }
  .racket .racket__right {
    margin: 0 auto;
    width: 95%;
  }
  .racket .racket__right .racket__right__imagenes {
    margin: 0;
    display: block;
  }
  .racket .racket__right .racket__right__imagenes .contenedor__imagen {
    margin-top: 2rem;
  }
}
.fondo {
  background-image: url("/build/img/action-athlete-ball-342361.jpg");
  background-size: cover;
  height: 58.6rem;
}

.footer {
  background-color: #cbe346;
  height: 20.3rem;
  display: flex;
}
.footer .footer__left {
  display: flex;
  flex-direction: column;
}
.footer .footer__left .navegacion {
  width: 45.1rem;
}
.footer .footer__left .navegacion a {
  margin-right: 2.5rem;
  color: #ffffff;
}

.footer__contenedor {
  margin-top: 6.4rem;
  margin-left: 7.8rem;
}

.social img {
  margin-right: 1.2rem;
  margin-top: 1.3rem;
}

.footer__right {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
.footer__right img {
  width: 5.5rem;
  height: 5.5rem;
  margin-top: 7.7rem;
  margin-right: 6.6rem;
}

@media (max-width: 480px) {
  .footer {
    display: block;
  }
  .footer .footer__left .footer__contenedor {
    margin: 4rem 0 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .footer .footer__left .footer__contenedor .navegacion {
    width: auto;
    margin-bottom: 1rem;
  }
  .footer .footer__left .footer__contenedor .navegacion a {
    margin-right: 1rem;
  }
  .footer .footer__right img {
    margin: 2rem auto;
  }
}